/**
 * Vivid Voice Agent - Server Utilities
 * 
 * Helpers for creating API routes in Next.js applications
 * Import from '@worldstreet/vivid-voice/server'
 */

import type { VoiceFunctionConfig } from './types'

// Re-export prompt utilities for server-side use (no React dependency)
export {
  VIVID_BASE_PROMPT,
  VIVID_TEST_PROMPT,
  buildSystemPrompt,
  generateFunctionInstructions,
} from './prompt'

// ============================================================================
// Types
// ============================================================================

export interface TokenRequestBody {
  userId?: string
  /** Current page pathname (e.g. '/about') */
  pathname?: string
  /** Pre-evaluated platform context string from platformContext callback */
  platformPrompt?: string
  /** User's first name for personalization */
  userName?: string
  /** User's last name */
  userLastName?: string
  /** User's email */
  userEmail?: string
}

export interface TokenResponse {
  client_secret: string
  expires_at: number
}

export interface FunctionRequestBody {
  name: string
  args: Record<string, unknown>
}

export interface FunctionResponse {
  success: boolean
  result?: unknown
  error?: string
}

export interface VividServerConfig {
  /**
   * OpenAI API key - read from environment by default
   */
  openAIApiKey?: string
  /**
   * Voice to use for responses
   * @default 'alloy'
   */
  voice?: string
  /**
   * System instructions for the session
   */
  instructions?: string
  /**
   * OpenAI Realtime model to use
   * @default 'gpt-4o-realtime-preview-2024-12-17'
   */
  model?: string
  /**
   * Tool definitions for the session
   */
  tools?: Array<{
    type: 'function'
    name: string
    description: string
    parameters: Record<string, unknown>
  }>
  /**
   * Turn detection configuration
   */
  turnDetection?: {
    type?: 'server_vad'
    threshold?: number
    prefix_padding_ms?: number
    silence_duration_ms?: number
  }
  /**
   * Validate request before generating token
   * Return false to reject the request
   */
  validateRequest?: (request: Request) => Promise<boolean> | boolean
  /**
   * Build instructions dynamically per request
   * Receives the parsed request body
   */
  buildInstructions?: (body: TokenRequestBody) => string | Promise<string>
  /**
   * Server-side functions that can be called via the bridge
   */
  functions?: VoiceFunctionConfig[]
}

// ============================================================================
// Token Endpoint Handler
// ============================================================================

/**
 * Create a token endpoint handler for Next.js App Router
 * 
 * @example
 * ```ts
 * // app/api/vivid/token/route.ts
 * import { createTokenHandler } from '@worldstreet/vivid-voice/server'
 * 
 * export const POST = createTokenHandler({
 *   openAIApiKey: process.env.OPENAI_API_KEY,
 * })
 * ```
 */
export function createTokenHandler(config: VividServerConfig = {}) {
  return async function handler(request: Request): Promise<Response> {
    try {
      // Validate request if validator provided
      if (config.validateRequest) {
        const isValid = await config.validateRequest(request)
        if (!isValid) {
          return new Response(
            JSON.stringify({ error: 'Unauthorized' }),
            { status: 401, headers: { 'Content-Type': 'application/json' } }
          )
        }
      }

      const apiKey = config.openAIApiKey || process.env.OPENAI_API_KEY

      if (!apiKey) {
        console.error('[Vivid Server] OPENAI_API_KEY not configured')
        return new Response(
          JSON.stringify({ error: 'Server configuration error' }),
          { status: 500, headers: { 'Content-Type': 'application/json' } }
        )
      }

      // Parse request body for dynamic instructions
      const body: TokenRequestBody = await request.json().catch(() => ({}))

      // Build instructions
      let instructions = config.instructions || 'You are Vivid, a helpful voice AI assistant.'
      if (config.buildInstructions) {
        instructions = await config.buildInstructions(body)
      }

      // Create ephemeral session with OpenAI Realtime API
      const sessionPayload: Record<string, unknown> = {
        model: config.model || 'gpt-4o-realtime-preview-2024-12-17',
        modalities: ['text', 'audio'],
        voice: config.voice || 'alloy',
        instructions,
        input_audio_transcription: {
          model: 'whisper-1',
        },
        turn_detection: config.turnDetection || {
          type: 'server_vad',
          threshold: 0.6,
          prefix_padding_ms: 400,
          silence_duration_ms: 600,
        },
      }

      // Add tools if configured
      if (config.tools && config.tools.length > 0) {
        sessionPayload.tools = config.tools
      }

      const sessionResponse = await fetch('https://api.openai.com/v1/realtime/sessions', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(sessionPayload),
      })

      if (!sessionResponse.ok) {
        const error = await sessionResponse.json().catch(() => ({ message: 'Unknown error' }))
        console.error('[Vivid Server] OpenAI session creation error:', error)
        return new Response(
          JSON.stringify({ error: 'Failed to create voice session', details: error }),
          { status: sessionResponse.status, headers: { 'Content-Type': 'application/json' } }
        )
      }

      const sessionData = await sessionResponse.json()

      const response: TokenResponse = {
        client_secret: sessionData.client_secret.value,
        expires_at: sessionData.client_secret.expires_at,
      }

      return new Response(JSON.stringify(response), {
        status: 200,
        headers: { 'Content-Type': 'application/json' },
      })
    } catch (error) {
      console.error('[Vivid Server] Token generation error:', error)
      return new Response(
        JSON.stringify({ error: 'Failed to generate token' }),
        { status: 500, headers: { 'Content-Type': 'application/json' } }
      )
    }
  }
}

// ============================================================================
// Function Execution Endpoint Handler
// ============================================================================

/**
 * Create a function execution endpoint handler for Next.js App Router
 * 
 * @example
 * ```ts
 * // app/api/vivid/function/route.ts
 * import { createFunctionHandler } from '@worldstreet/vivid-voice/server'
 * import { serverFunctions } from '@/lib/vivid-functions'
 * 
 * export const POST = createFunctionHandler({
 *   functions: serverFunctions,
 * })
 * ```
 */
export function createFunctionHandler(config: VividServerConfig = {}) {
  const functionMap = new Map(
    (config.functions ?? []).map((fn) => [fn.name, fn])
  )

  return async function handler(request: Request): Promise<Response> {
    try {
      // Validate request if validator provided
      if (config.validateRequest) {
        const isValid = await config.validateRequest(request)
        if (!isValid) {
          return new Response(
            JSON.stringify({ success: false, error: 'Unauthorized' }),
            { status: 401, headers: { 'Content-Type': 'application/json' } }
          )
        }
      }

      const body: FunctionRequestBody = await request.json()
      const { name, args } = body

      if (!name) {
        return new Response(
          JSON.stringify({ success: false, error: 'Function name required' }),
          { status: 400, headers: { 'Content-Type': 'application/json' } }
        )
      }

      const fn = functionMap.get(name)

      if (!fn) {
        return new Response(
          JSON.stringify({ success: false, error: `Function "${name}" not found` }),
          { status: 404, headers: { 'Content-Type': 'application/json' } }
        )
      }

      // Execute the function
      const result = await fn.handler(args)

      const response: FunctionResponse = {
        success: true,
        result,
      }

      return new Response(JSON.stringify(response), {
        status: 200,
        headers: { 'Content-Type': 'application/json' },
      })
    } catch (error) {
      console.error('[Vivid Server] Function execution error:', error)
      
      const response: FunctionResponse = {
        success: false,
        error: error instanceof Error ? error.message : 'Function execution failed',
      }

      return new Response(JSON.stringify(response), {
        status: 500,
        headers: { 'Content-Type': 'application/json' },
      })
    }
  }
}

// ============================================================================
// Clerk Integration Helpers
// ============================================================================

/**
 * Create a request validator that checks Clerk authentication
 * 
 * @example
 * ```ts
 * import { createClerkValidator } from '@worldstreet/vivid-voice/server'
 * import { auth } from '@clerk/nextjs/server'
 * 
 * export const POST = createTokenHandler({
 *   validateRequest: createClerkValidator(auth),
 * })
 * ```
 */
export function createClerkValidator(
  authFn: () => Promise<{ userId: string | null }> | { userId: string | null }
) {
  return async (): Promise<boolean> => {
    try {
      const { userId } = await authFn()
      return userId !== null
    } catch {
      return false
    }
  }
}

